<template>
  <v-chart :options="option"/>
</template>

<style>
/**
 * The default size is 600px×400px, for responsive charts
 * you may need to set percentage values as follows (also
 * don't forget to provide a size for the container).
 */
</style>

<script>
import ECharts from 'vue-echarts'
import 'echarts/lib/chart/pie'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/legend'

export default {
  name: 'PieChart',
  props: {
    values: {
        default(){
            return []
        }
    },
  },
  components: {
    'v-chart': ECharts,
  },
  data () {
    return {
      option: {
          title: {
              show: false,
          },
          tooltip: {
              trigger: 'item',
              formatter: '{b} : {c} ({d}%)'
          },
          legend: {
              type: 'plain',
              orient: 'vertical',
              right: 75,
              icon: 'circle',
              top: 27,
              bottom: 20,
              itemWidth: 12,
              itemHeight: 12,
              borderRadius: 3,
              itemGap: 13,
              show: true,
              textStyle:{
                fontSize: 14,
                fontFamily: 'prox',
                lineHeight: 10
              },
          },
          series: [
              {
                  type: 'pie',
                  radius: '100%',
                  hoverOffset: 0,
                  center: ['19%', '50%'],
                  data: [{name: 'Мобайл', value: this.values[0], itemStyle: {color: '#DB5A5A'}},
                  {name: 'Десктоп', value: this.values[1], itemStyle: {color: '#27AE60'}}],
                  label: {
                      show: false
                  }
              }
          ]
      }
    }
  }
}
</script>
