<template>
    <ul class="nav-list">
        <li class="nav-list-item" v-if="section.count > 0" v-for="section in sections" :class="{'active': $route.params.sid == section.id, 'unactive': !section.count}">
            <a  class="nav-list-item-link dropdown"
                :class="{'open': section.show}"
                href="javascript:void(0);"
                @click="toggleSection(section.id)"
                v-if="section.sections.size > 0">{{ section.name }}<span class="num">{{ section.count }}</span></a>
            <router-link v-else
                        :tag="!section.count ? 'span':'a'" :to="{ name: 'catalogList', params: { sid: section.id }}"
                         class="nav-list-item-link"
                         @click.native="activeSection(section.id)"
                         active-class="active"
            >{{ section.name }}<span class="num">{{ section.count }}</span></router-link>

            <catalog-sections-tree v-show="section.show" v-if="section.sections.size > 0" :sections="section.sections"></catalog-sections-tree>
        </li>
    </ul>
</template>

<script>
    import { mapState } from 'vuex'
    import {scrollTo} from '@/helpers/animate.js';

    export default {
        name: "CatalogSectionsTree",
        props: {
            sections: {
                default: function () {
                    return []
                }
            }
        },
        data: function() {
            return {
              aside: null,
              newBlock: null,
              fixedMargin: null,
              z: null,
              marginTop: 24,
              marginBottom: 24,
            }
        },
        created () {

        },
        watch:{
          '$route'(to,from){
              if(this.newBlock){
                  this.newBlock.className = 'stop';
                  this.newBlock.style.top = '0px';
              }
          }
        },
        mounted(){

        },
        computed: mapState({
            previousSection: state => state.actions.previousSection
        }),
        methods: {
            activeSection: function (id) {
                if(id && this.previousSection !== id){
                    this.$store.commit('actions/previousSection', id);
                    this.$store.dispatch('actions/activeCategory', {
                        currentSection:id,
                        isLoad: true,
                    });
                }
                if(+id === +this.$route.params.sid){
                    scrollTo('up');
                }
            },
            toggleSection: function (id) {
                if(id){
                    this.$store.commit('actions/toggleSection', {currentSection:id});
                }
            },
        }
    }
</script>

<style>
    .nav-list .nav-list {
        display: block;
    }
</style>
