<template>
  <v-chart :options="option"/>
</template>

<style>
/**
 * The default size is 600px×400px, for responsive charts
 * you may need to set percentage values as follows (also
 * don't forget to provide a size for the container).
 */
.echarts {
  width: 965px;
  height: 250px;
}
</style>

<script>
import ECharts from 'vue-echarts'
import 'echarts/lib/chart/line'
import 'echarts/lib/component/tooltip'

export default {
  name: 'LineChart',
  props: {
    months: {
        default(){
            return []
        }
    },
    incomes: {
        default(){
            return []
        }
    },
    maxProfit: {
        default: 100000
    },
  },
  components: {
    'v-chart': ECharts,
  },
  data () {
    return {
      option: {
        grid: {
          width: '936px',
          left: '11px',
          height: '160px'
        },
        title: {
          show: false,
        },
        legend: {
          show: false,
        },
          xAxis: {
              type: 'category',
              data: this.months,
              splitLine: {
                  show: true,
                  lineStyle: {
                    color: ['#eee']
                  },
              },
              boundaryGap: false,
              axisLine: {
                  show: false
              },
              axisTick: {
                  show: false
              },
              axisLabel: {
                  color: '#aaaaaa',
                  fontSize: 10,
                  fontFamily: 'prox',
                  margin: 10
              },
          },
          yAxis: {
            splitLine: {
                show: true,
                lineStyle: {
                  color: ['#eee']
                },
            },
              type: 'value',
              axisLine: {
                  show: false
              },
              axisTick: {
                  show: false
              },
              axisLabel: {
                  show: false
              },
              min: 0,
              max: +this.maxProfit
          },
          tooltip: {
            trigger: 'item',
            formatter: '{b} : {c}',
            backgroundColor: 'transparent',
            position: function (point, params, dom, rect, size) {
                // fixed at top
                return [rect.x + 'px', rect.y + 'px'];
             }
            // function (point, params, dom, rect, size) {
                // fixed at top
                // return [point[0], point[1]];
            //},
          },
          series: [{
              data: this.incomes,
              type: 'line',
              symbol: 'circle',
              symbolSize: 7,
              itemStyle: {
                color: '#2463B8',
                borderColor: '#2463b8',
              },
              lineStyle: {
                  color: '#2463B8',
                  width: 1
              },
          }]
      }

    }
  }
}
</script>
