<template>
    <div class="coupon-details__stat">
        <div class="col-left">
            <div class="coupon-details__stat-row">
                <div class="line">
                    <div class="l">Оформлено</div>
                    <div class="r">{{ action.statistics.sold }}</div>
                </div>

                <div class="n">Общее количество купонов</div>
            </div>

            <div class="coupon-details__stat-row">
                <div class="line">
                    <div class="l">Использовано</div>
                    <div class="r">{{ action.statistics.ext }}</div>
                </div>

                <div class="n">Купоны, по которым получена обещанная скидка</div>
            </div>

            <div class="coupon-details__stat-row">
                <div class="line">
                    <div class="l">Ждут своего часа</div>
                    <div class="r">{{ action.statistics.valid }}</div>
                </div>

                <div class="n">Оформленные, но еще не использованные купоны</div>
            </div>

            <!--<div class="coupon-details__stat-row">
                <div class="line">
                    <div class="l">Просрочено</div>
                    <div class="r">{{ action.statistics.exp }}</div>
                </div>

                <div class="n">Купоны, которыми не воспользовались вовремя</div>
            </div>-->


            <div class="coupon-details__stat-row">
                <div class="line">
                    <div class="l">Аннулировано поставщиком</div>
                    <div class="r">{{ action.statistics.canceledProvider }}</div>
                </div>

                <div class="n">Купоны, которые отозвал поставщик</div>
            </div>

            <div class="coupon-details__stat-row">
                <div class="line">
                    <div class="l">Аннулировано модератором</div>
                    <div class="r">{{ action.statistics.canceledModerator }}</div>
                </div>

                <div class="n">Купоны, отозванные нами</div>
            </div>

            <div class="coupon-details__stat-row">
                <div class="line">
                    <div class="l">Отменено</div>
                    <div class="r">{{ action.statistics.withdrawn }}</div>
                </div>

                <div class="n">Купоны, от которых отказались сами покупатели</div>
            </div>
        </div>



        <div class="col-right">

            <div class="coupon-details__stat-card">
                <div class="icon">
                    <span class="i i-discount2" style=""></span>
                </div>

                <div class="t">
                    Всего сэкономлено
                </div>
                <div class="d">{{action.statistics.sumFormat}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></div>
                <div class="n" v-if="+action.statistics.ext === 0">Потому что погашено {{ action.statistics.ext }} купонов</div>
                <div class="n" v-else>после погашения {{ action.statistics.ext }} {{ declOfNumber(action.statistics.ext,['купона','купонов','купонов']) }}</div>
            </div>


           <!-- <div class="coupon-details__stat-card">
                <div class="icon">
                    <span class="i i-trophy" style="transform: scale(1.25);"></span>
                </div>

                <div class="t">
                    Позиция в рейтинге
                </div>

                <div class="d">#287</div>
                <div class="n">по сумме скидок</div>

                <div class="d">#158</div>
                <div class="n">по количеству оформлений</div>
            </div>-->


            <div class="coupon-details__stat-card" v-if="+action.status === 19">
                <div class="icon">
                    <span class="i i-calendar"></span>
                </div>

                <div class="t">
                    На сайте
                </div>

                <div class="d">{{ action.dateDiff}}</div>
                <div class="n">Опубликовано {{ action.statusDate}}</div>
            </div>
            <div class="coupon-details__stat-card" v-else>
                <div class="icon">
                    <span class="i i-calendar"></span>
                </div>

                <div class="t">
                    На сайте
                </div>

                <div class="d">0</div>
                <div class="n">Акция не опубликована</div>
            </div>
        </div>
    </div>
    <!-- end coupon-details__stat -->
</template>

<script>
    import {declOfNumber} from '@/helpers/formatted';

    export default {
        name: "StatisticsDetail",
        props: {
            action: {
                default(){
                    return {}
                }
            }
        },
        methods: {
            declOfNumber(number, titles){
                return declOfNumber(number, titles);
            },
        }
    }
</script>

