<template>
  <div>
    <main-banner v-if="$route.path === '/'"/>
    <div class="content">
      <catalog v-if="!isEmptyLoad"></catalog>
      <div v-else class="container">
          <div class="container__full">
                <div class="wrapper-content">
                    <div class="sect-blank">
                        <div class="wrap text-center">
                            <div class="text-size-l text-color-grey text-bolder">К сожалению, у нас пока нет партнёров в г. {{ currentCity.name }}...</div>
                            <div class="mt24">
                                <router-link to="/cooperation">Стать партнёром</router-link>
                                    <a class="ml32" href="javascript:void(0);" @click="openCitySelect()">Выбрать другой город</a>
                            </div>
                        </div>
                    </div>
                    <!-- end sect-blank -->
                </div>
          </div>
      </div>
    </div>
  </div>

</template>

<script>
    import Catalog from '@/components/catalog/Catalog.vue'
    import MainBanner from '@/components/MainBanner.vue'
    import { mapState } from 'vuex'

    export default {
        metaInfo: {
            title: 'СКИДКИ АЛЕКСЕЯ ЗЕМСКОВА — ДЛЯ ВСЕХ! | ZEMSKIDKI.RU',
            /*meta: [
                { vmid: 'description', property: 'description', content: 'СКИДКИ АЛЕКСЕЯ ЗЕМСКОВА — ДЛЯ ВСЕХ' },
                { vmid: 'og:title', property: 'og:title', content: 'СКИДКИ АЛЕКСЕЯ ЗЕМСКОВА — ДЛЯ ВСЕХ' },
                { vmid: 'og:description', property: 'og:description', content: 'СКИДКИ АЛЕКСЕЯ ЗЕМСКОВА — ДЛЯ ВСЕХ' },
            ],*/
        },
        name: "Home",
        components: {
            MainBanner,
            Catalog
        },
        computed: {
            ...mapState({
                loadAction: state => state.actions.isLoad,
                isLoadNextPage: state => state.actions.isLoadNextPage,
                coupons: state => state.actions.coupons,
                popups: state => state.popups.items,
                currentCity:  state => state.cities.current,
            }),
            isEmptyLoad () {
                return !this.isLoadNextPage && !this.loadAction && !(this.coupons.length > 0);
            },

        },
        mounted() {
            this.$store.commit('actions/isLoadNextPage', true);
            /*if(this.$store.getters['users/firstVisit']){
                this.$router.push('/about/');
                this.$store.commit('users/setCookiesFirstVisit');
            }*/
            ym(39511350, 'hit', this.$route.path, {title: 'СКИДКИ АЛЕКСЕЯ ЗЕМСКОВА — ДЛЯ ВСЕХ!'});
        },
        methods: {
            openCitySelect() {
                this.$store.dispatch('popups/open', {name: this.popups.citySelect.name});
            },
        }
    }
</script>
