<template>
  <div class="popup-info popup-icon-info">
    <svg class="big-icon">
      <use xlink:href="@/assets/img/big-icons-sprite.svg#emoji-opinion-error"></use>
    </svg>


      <div class="text mt32">
          <div class="text-size-l text-color-red text-bold info-title">Вы не можете написать отзыв на этот купон,<br>потому что вы его уже написали</div>

          <p class="info-text">
              Написать отзыв на купон можно только один раз<br>и редактировать его после уже нельзя.<br><br>
              P.S. При этом вы всегда можете <a href="javascript:void(0);" class="link">пожаловаться на купон</a> руководителю проекта.
          </p>
      </div>

      <div class="mt32">
          <span class="btn btn--white" @click="close">Теперь я тоже так считаю....</span>
      </div>
  </div>
</template>

<script>
    import Popup from '@/components/modal/Popup.vue'
    import {mapState} from 'vuex'

    export default {
        name: "OpinionErrorSend",
        methods: {
            close() {
                this.$store.commit('popups/close', {name: this.popups.opinionAdd.name});
            },
            openCouponClaim: function () {
                this.$store.commit('popups/close', {name: this.popups.opinionAdd.name});
                this.$store.dispatch('popups/open', {name: this.popups.couponClaim.name});
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

