<template>
    <div :class="{'coupon-container':couponModal,'coupon':!couponModal}">
        <div class="coupon" :class="{'coupon':couponModal}">
             <tooltip
                     :direction="'left-center-direction'"
                     :add-class-wrap="'coupon__bookmark-wrap'"
                     :add-style="{width: 248 + 'px', 'top': '67px', 'right': '126px'}">
                <div slot="tooltipBtn" class="coupon__bookmark">
                    <div class="wrap">
                        <span class="d">−{{ item.discountFrom }}%</span>
                        <span class="p">
                            ≈{{ item.realSavingsFormat }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                        </span>
                    </div>
                </div>

                <div slot="tooltipContent" style="font-size: 16px; ">
                    При обращении к этому<br>
                    поставщику средний чек<br>
                    составляет <strong>{{ item.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>,<br>
                    а экономия – около
                    <strong>{{ item.realSavingsFormat }}
                    <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>
                </div>
            </tooltip>

            <a v-if="isModerator" target="_blank" :href='"/moderator/coupons/"+ actionStatusCode +"/id"+item.id+"/"' class="coupon__link"></a>
            <router-link v-else @click.native="activeItem(item.id)"
                         :to="{ name: 'catalogDetail', params: { id: item.id }}"
                         class="coupon__link"></router-link>

            <span v-if="item.isNew" class="coupon__sticker">new</span>
            <span class="coupon__mini-status" v-if="isModerator">{{ actionStatus }}</span>

            <div class="coupon__wrap">
                <div class="coupon__img"
                     :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+item.image+')' }"></div>
                     
                <div class="coupon__content">
                    <div class="coupon__favorite" v-if="!isModerator">
                        <a target="_blank" v-if="user.access_rights && user.access_rights.is_moderator"
                           :href="'/moderator/coupons/id'+item.id+'/#open_catalog_detail'" class="i i-book2 icon-btn"></a>
                        <span v-else
                              ref="favoriteBtn" @click="setFavorite(item)" class="check-favorites"
                              :class="{active: item.favorite}"></span>
                    </div>
                    
                    <div class="coupon__stat">
                        <span class="elem --views">
                            <span class="i i-eye"></span>
                            <span class="t">{{ item.showCounterFormat }}</span>
                        </span>

                        <span class="elem --buy" v-if="item.statistics.sold > 0">
                            <span class="i i-cart"></span>
                            <span class="t">{{ item.statistics.sold }}</span>
                        </span>

                        <!--<span class="elem &#45;&#45;comment" v-if=" item.countOpinions > 0">
                            <span class="i i-comment"></span>
                            <span class="t">{{  item.countOpinions }}</span>
                        </span>-->
                    </div>

                    <div class="coupon__name">{{ item.name }}</div>

                    <div class="coupon__company">{{ item.provider.name }}</div>
                    
                    <div class="w">
                        <div class="coupon__economy">
                            <div class="t">
                                Экономия
                                
                                <tooltip v-if="!isModerator" 
                                         :direction="'bottom-right-direction'"
                                         :add-class-wrap="'dib mt-2'"
                                         :add-style="{width: 376 + 'px'}">
                                    <span slot="tooltipBtn" class="i i-question3 icon-btn"></span>
                                    
                                    <div slot="tooltipContent">
                                        <p><strong>Как рассчитывается экономия? Очень просто:</strong></p>

                                        <ul class="ul-disc-small">
                                            <li>Средний чек у данного поставщика составляет {{ item.averageCheckFormat }}
                                                <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                            <li>Предоставляемая скидка составляет {{ item.discountFrom }}%</li>
                                            <li>{{ item.discountFrom }}% от {{ item.averageCheckFormat }} <span
                                                    class="rouble">{{ $store.getters['options/setRouble'] }}</span> – это примерно {{ item.realSavingsFormat }}
                                                <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                        </ul>

                                        <p>То есть, при обращении к данному поставщику экономия обычно составляет ≈{{
                                            item.realSavingsFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></p>
                                    </div>
                                </tooltip>
                            </div>

                            <div class="p">
                                ≈{{ item.realSavingsFormat }}
                                <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                            </div>
                        </div>
                        
                        
                        <div class="coupon__info">
                            Средний чек
                            {{ item.averageCheckFormat }}
                            <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>,

                            скидка
                            <span class="n --text" v-if="item.instrumentAction">Бесплатная <br> доставка</span>
                            <span class="n" v-else>{{ item.discountFrom }}%</span>
                        </div>

                    </div>
                </div>
            </div>
            <!-- end coupon__wrap -->
        </div>
        
        <div class="wrapper-content mt32" v-if="isModerator">
            <div class="wrapper-content-inner">
                <div v-if="item.cityCount > 1" class="coupon-bottom__city">Городов: {{ item.cityCount }}</div>
                <div v-else class="coupon-bottom__city">{{(item.cityName)?item.cityName:'Нет' }}</div>

                <template v-if="item.sections_tree.length > 0 ">
                    <div v-for="(sect, index) in item.sections_tree" class="coupon-bottom__category" :key="'tree'+index">
                        <div class="c">
                            <template v-for="(secItem, itemIndex) in sect">
                                <span :key="'treeItem'+itemIndex" class="c__i">{{secItem}}</span>{{((itemIndex+1)!=sect.length)?'/':''}}
                            </template>
                        </div>
                    </div>
                </template>
                <div v-if="item.sections_tree.length == 0 && item.status != 17" class="coupon-bottom__category">
                    <div class="c">
                        <a @click.prevent="openCategoryChoice(item)" href="" class="c__i">Добавить привязку к каталогу</a>
                    </div>
                </div>

                <div v-if="item.history && item.history.length > 0">
                    <div class="mt16" :key="history.id" v-for="history in item.history">
                        <div v-html="history.text"></div>
                        <div class="mt8 text-color-grey text-size-xs">
                            {{history.date}}, {{history.group}}: {{history.user}}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!-- end coupon -->
</template>

<script>
    import {flyToFavorite} from '@/helpers/animate';
    import Tooltip from '@/components/Tooltip.vue';
    import {mapState} from 'vuex';
    
    export default {
        name: "CatalogListItem",
        components: {
            Tooltip
        },
        props: {
            item: {
                default: function () {
                    return {}
                }
            },
            isModerator: {
                default: function () {
                    return false
                }
            },
            isSimilar: {
                default: function () {
                    return false
                }
            },
            couponModal: {
                default: function () {
                    return true
                }
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
                coupons(){
                    return this.$route.name === 'favorites' ? this.$store.getters['actions/getFavorites']() : this.$store.state.actions.coupons;
                },
                actionMenuList: state => state.menus.itemsAction,
            }),
            actionStatus() {
                if(this.item.statusModerator == 58){
                    return "Новая "+this.item.dateDiffStatus;
                }
                else{
                    return this.item.statusName+" "+this.item.dateDiffStatus;    
                }
            },
            actionStatusCode() {
                let statusId = +this.item.statusModerator,
                    statusCode = 'unsorted';

                for (let itemIndex in this.actionMenuList['coupons']) {
                    if (this.actionMenuList['coupons'][itemIndex]['id'] === statusId) {
                        statusCode = this.actionMenuList['coupons'][itemIndex]['code'];
                    }
                }

                return statusCode;
            },
        },
        methods: {
            activeItem: function (id) {
                if(id){
                    this.$store.dispatch('actions/goCoupon', {id:id});
                }
            },
            setFavorite: function (item) {
                let action = {...item};
                if(this.isSimilar){
                    action.isSimilar = true;
                }
                this.$store.dispatch('actions/setFavorite', action);
                
                flyToFavorite(this.$refs.favoriteBtn);
                if(this.$route.name === 'favorites') {
                    if(this.coupons.length > 0){
                        this.$store.dispatch('actions/getSectionFavorites');
                    } else {
                        this.$store.commit('actions/setSectionFavorites', []);
                    }

                }
            },
            openCategoryChoice: function(item){
                this.$store.commit('moderatorCategories/setItem', {
                    id: item.id,
                    name: item.name
                });
                this.$store.dispatch('popups/open', {name: this.popups.categoryChoice.name});
            },
        }
    }
    
</script>