<template>
    <div>
        <div class="popup__header">
            <div class="title">Написать отзыв на купон</div>

            <div class="subtitle" v-html="action.name"></div>
            <div class="text-color-white-trans mt16">
                INTERNO
            </div>
        </div>


        <div class="popup__content">
            <h2>Какой отзыв вы хотите написать?</h2>

            <div class="mt16">
                <div class="opinion-rate" data-btn-group="group1">
                    <input v-model="checkedPositive" type="checkbox" class="opinion-rate__like"/>
                    <label @click="checkedNegative = false;checkedPositive=true;">
                      <svg class="big-icon">
                        <use xlink:href="@/assets/img/big-icons-sprite.svg#like"></use>
                      </svg>
                        <span class="t">Позитивный</span>
                    </label>

                    <input v-model="checkedNegative" type="checkbox" class="opinion-rate__dislike" />
                    <label @click="checkedNegative = true;checkedPositive=false;">
                        <svg class="big-icon">
                          <use xlink:href="@/assets/img/big-icons-sprite.svg#like"></use>
                        </svg>
                        <span class="t">Негативный</span>
                    </label>
                </div>
            </div>



            <div class="h2 mt24">Текст отзыва</div>

            <div class="mt16">
                <textarea-field  :placeholder="text.placeholder"
                                 v-model="text.value"
                                 :auto-focus="true"
                                 :is-error="text.error"
                                 @focus="removeError(text.name)"
                ></textarea-field>
            </div>




            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <button class="btn" @click="sendForm">
                        <span>Опубликовать</span>
                    </button>
                </div>


                <div class="row-b__col elem-pos-fit">
                    Оставить отзыв к купону можно только один раз<br>
                    и изменить его после уже нельзя!
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'

    export default {
        name: "OpinionAdd",
        components: {
            TextareaField
        },
        props: {
            action: {
                default() {
                    return {}
                }
            }
        },
        data() {
            return {
                checkedPositive: true,
                checkedNegative: false,
                text: {
                    name: 'text',
                    value: '',
                    required: true,
                    error: false,
                    disabled: false,
                    placeholder: 'Опишите реальный опыт использования купона',
                },
            }
        },
        methods: {
            sendForm() {
                this.text.error = false;
                if(this.text.value.length < 10){
                    setTimeout(()=>{
                        this.text.error = true;
                    },150);
                } else {
                    this.$store.dispatch('opinions/sendForm', {
                        text: this.text.value,
                        actionId: this.action.id,
                        type: this.checkedPositive ? 'positive' : 'negative'
                    });
                    this.$store.commit('popups/close', {name: this.popups.opinionAdd.name});
                }
            },
            removeError (field) {
                this[field].error = false;
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

