<template>
    <section v-if="visited && visitedItems.length > 0 || !visited" class="" :class="{container__full: favorites && isEmptyLoad, container__left:(favorites && !isEmptyLoad),container__right:!favorites && !visited}">
        <div v-if="visited" class="content-title" style="margin-top: 40px;">
            <div class="content-title__wrap">
                <div class="content-title__text text-size-xl text-center">Вы недавно смотрели</div>
            </div>
        </div>


        <main-title :show-map="!isEmptyLoad"
                    :title="title"
                    v-if="(!favorites) && !visited"
                    :class="{'--blue': isIndexPage}"></main-title>

        <catalog-map v-if="!isEmptyLoad && coupons.length > 0 && !favorites"
                     :markers="markers"
                     :viewAllMarkers="true"
                     :count-other="coupons.length - markers.length"
                     :coords="currentCity.coords"
        ></catalog-map>

        <catalog-sort :is-load="loadAction || !init" v-if="!isEmptyLoad && !favorites && !visited || !init && !favorites && !visited "
                       :favorites="favorites"></catalog-sort>


        <div class="coupon-list container-scroll" v-if="!isEmptyLoad || !init" :class="{mt0:favorites}" id="coupon-list">
            <catalog-list-item v-for="coupon in coupons"
                               :item="coupon"
                               v-if="((favorites && coupon.favorite) || !favorites) && !(visited && +coupon.id === +$route.params.id)"
                               :key="coupon.id">
            </catalog-list-item>
        </div>
        <div :class="{mt32:coupons.length > 0}" v-if="loadAction || !init" class="wrapper-content pb0 pt0">
            <catalog-ghost></catalog-ghost>
        </div>
        <div v-else-if="errorLoad && coupons.length == 0" class="sect-blank wrapper-content mt24" style="min-height: 350px; ">
            <div class="text-center">
                <div class="text-center text-color-grey">
                    <span style="font-size: 106px; font-weight: 900; line-height: 102px; ">:(</span>
                </div>

                <div class="text-size-l text-bolder text-color-grey mt24">Произошла ошибка загрузки данных</div>
                <div class="mt16">
                    <a @click="reload" href="javascript:void(0)" class="link-icon">
                        <span class="i i-refresh"></span>
                        <span class="text">Перезагрузить страницу</span>
                    </a>
                </div>
            </div>
        </div>
        <div v-else-if="isEmptyLoad">
            <div v-if="favorites" class="no-favorites">
                <div class="text-center">
                    <svg class="big-icon">
                      <use xlink:href="@/assets/img/big-icons-sprite.svg#emoji-no-favorites"></use>
                    </svg>
                    <div class="text-size-xxxl text-bold mt32">У вас нет избранных купонов</div>
                    <div class="text mt16">Чтобы они были – добавьте их из <router-link to="/catalog/">каталога</router-link></div>
                </div>
            </div>
            <div class="sect-blank" v-else>
                <div class="wrap text-center">
                    <div class="text-size-l text-bolder text-color-grey mt16">К сожалению, в вашем городе<br>по этому направлению нет ни одной акции:(</div>

                    <div class="mt16 text-color-grey">
                        Кстати, если не получается стать покупателем — можете попробовать <router-link to="/cooperation/" class="link-icon" >стать поставщиком</router-link>...
                    </div>

                    <div class="mini-zems"></div>
                </div>
            </div>
            <!-- end sect-blank -->
        </div>

    </section>

    <!-- end coupon-list -->
</template>

<script>
    import { mapState } from 'vuex'
    import CatalogListItem from '@/components/catalog/CatalogListItem.vue'
    import CatalogMap from '@/components/catalog/CatalogMap.vue'
    import CatalogGhost from '@/components/catalog/CatalogGhost.vue'
    import CatalogSort from '@/components/catalog/CatalogSort.vue'
    import MainTitle from '@/components/MainTitle.vue'
    import Tooltip from '@/components/Tooltip.vue';
    import Loader from '@/components/Loader.vue';

    export default {
        name: "CatalogList",
        components: {
            CatalogListItem,
            CatalogMap,
            CatalogSort,
            CatalogGhost,
            MainTitle,
            Loader,
            Tooltip
        },
        props: {
            favorites: {
                default: function () {
                    return false
                }
            },
            visited: {
                default: function () {
                    return false
                }
            },
        },
        created () {
            this.init = false;
            if(!(this.favorites && this.favoritesItems.length === 0 ) && !(this.visited && this.visitedItems.length === 0 ) || !this.favorites && !this.visited) {
                this.$store.dispatch('actions/getCouponList', {
                    currentSection: this.currentSection,
                    favorites: this.favorites,
                    visited: this.visited,
                    reload: true
                }).then( (e)=> {
                    this.$nextTick(function () {
                        this.init = true;
                    })
                })
            } else {
                this.$store.commit('actions/isLoadNextPage',false)
                this.$nextTick(function () {
                    this.init = true;
                })
            }
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
        },
        beforeDestroy() {
            this.init = false;
            window.removeEventListener('scroll', this.handleScroll);
        },
        computed: {
            title () {
                return this.favorites ? 'Избранное' : this.$store.getters['actions/getTitle'](this.currentSection);
            },
            isEmptyLoad () {
                return !this.loadAction && !(this.coupons.length > 0) && this.init;
            },
            currentSection () {
                return this.$route.params.sid;
            },
            markers () {
                let markers = [];
                for(let coupon of this.coupons) {
                    if(coupon.address.length > 0) {
                        for (let addrIndex in coupon.address) {
                            if (!coupon.address[addrIndex].coords) {
                                continue;
                            }
                            let coord = coupon.address[addrIndex].coords.split(',');
                            markers.push(
                                {
                                    id: coupon.id + '-' + addrIndex,
                                    type: 'placemark', // define properties here
                                    coords: [coord[1], coord[0]], // define options here
                                    coupon: {
                                        image: '//api.zemskidki.ru' + coupon.image,
                                        name: coupon.name,
                                        id: coupon.id
                                    }
                                }
                            );
                        }
                    }

                }
                return markers;
            },
            ...mapState({
                loadAction: state => state.actions.isLoad,
                errorLoad: state => state.actions.errorLoad,
                currentCity:  state => state.cities.current,
                favoritesItems: state => state.actions.favorites.items,
                visitedItems: state => state.actions.visited.items,
            }),
            coupons(){
                return this.favorites ? this.$store.getters['actions/getFavorites']() : this.$store.state.actions.coupons;
            },
            isIndexPage() {
                return this.$route.path === '/'
            },
        },
        data(){
            return {
                coords: [],
                init: false,
            }
        },
        methods: {
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.body.clientHeight;
                if(bodyHeight-80 <= scroll + viewport && this.init){
                    this.$store.dispatch('actions/getCouponList', {
                        currentSection: this.currentSection,
                        favorites: this.favorites,
                        visited: this.visited,
                    })
                }
            },
            reload(){
                window.location.href = window.location.href;
            }
        }

    }
</script>
