<template>
    <div :class="{'opinions--empty': opinions.length === 0}">
        <div class="opinions-list">
            <div class="opinion"
                 :class="{'opinion--processed':opinion.answer.text !== ''}"
                 v-for="opinion in opinions">
                <div class="opinion__author" :class="{'--dislike':opinion.type === 'negative'}">
                    <div class="rate">
                        <span class="i i-like"></span>
                    </div>

                    <div class="wrap">
                        <div class="wrap__top">
                            <div class="name">{{ opinion.user.name }}</div>

                            <div class="date">({{ getCityName(opinion.city)}})&nbsp;&nbsp; {{ opinion.date }}</div>
                        </div>


                        <div class="wrap__text">
                            <p v-html="opinion.text"></p>
                        </div>
                    </div>
                </div>
                <!-- end opinion__author -->
                
                
                <div class="opinion__feedback" v-if="opinion.answer.text !== ''">
                    <div class="rate">
                        <span class="i i-pointer3"></span>
                    </div>

                    <div class="wrap">
                        <div class="wrap__top">
                            <div class="name" v-html="opinion.provider.name"></div>

                            <div class="date">Через {{ opinion.answer.date }}</div>
                        </div>


                        <div class="wrap__text">
                            <p v-html="opinion.answer.text"></p>
                        </div>
                    </div>
                </div>
                <!-- end opinion__author -->
            </div>
            <!-- end opinion -->
        </div>


        <div v-if="isLoad || opinions.length === 0" class="sect-blank" style="min-height: 328px; ">
            <div class="wrap text-center">
                <loader v-if="isLoad"></loader>
                
                <div class="text-size-l text-bolder text-color-grey mt16" v-else>На эту акцию пока нет отзывов. <br> Но это временно...</div>
            </div>
        </div>
    </div>

</template>

<script>
    import {mapState} from 'vuex';
    import Loader from '@/components/Loader.vue';

    export default {
        name: "OpinionList",
        components: {
            Loader
        },
        props: {
            actionId: {
                default() {
                    return 0
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                opinions: state => state.opinions.items,
                isLoad: state => state.opinions.isLoad,
            }),
        },
        mounted() {
            this.$store.commit('opinions/setData', {items: []});
            this.$store.commit('opinions/isLoadNextPage', true);
            this.$store.dispatch('opinions/getList', {actionId: this.actionId});
        },
        methods: {
            getCityName(xmlId) {
                let city = this.$store.getters['cities/getByXmlId'](xmlId)
                return city.name;
            }
        }
    }
</script>