<template>
	<section class="" :class="{container:viewType !== 'detail'}">
		<aside-sticky :marginTop="24" :marginBottom="24" v-if="viewType === 'list' && !favorites">
			<aside class="aside aside--left aside--catalog" >
				<div class="aside__sticky" id="aside-sticky">
					<div class="nav nav--city">
						<catalog-sections></catalog-sections>
					</div>

					<div class="nav mt24" style="padding: 24px;" v-if="(sectionsCountNotEmpty > 0 && sectionsCountNotEmpty < 7) || (totalCoupons > 0 && totalCoupons < 25)">
						<div class="text-size-l text-bold mb24">
							Вы заметили, что
							в&nbsp;вашем городе
							очень мало акций?
						</div>

						<div class="mb24">
							Чтобы их было больше, расскажите<br>
							об этом проекте поставщикам<br>
							своего города. Они получат<br>
							бесплатное продвижение, а взамен<br>
							дадут нам с вами настоящие скидки.
						</div>

						<div class="mb16">
							<router-link to="/cooperation" class="link-icon">
								<i class="i i-man3"></i>
								<span class="text ml8">Стать партнёром</span>
							</router-link>
						</div>

						<div class="mt16">
							 <tooltip
									:direction="'bottom-center-direction'"
									:add-class-wrap="'dib'"
									:is-btn-close="false"
									ref="tooltipCopyLink"
									:add-style="{width: 150 + 'px'}">
								 <a slot="tooltipBtn" @click="copyLink()" class="link-icon" href="javascript:void(0)">
								 	<i class="i i-send"></i>
								 	<span class="text ml8">Поделиться ссылкой</span>
								 </a>

								<div slot="tooltipContent">
									<div class="text-size-s">Ссылка скопирована</div>
								</div>
							</tooltip>
						</div>
					</div>
				</div>
			</aside>
		</aside-sticky>
		<catalog-list :favorites="favorites" v-if="viewType === 'list'"></catalog-list>
		<catalog-detail v-else-if="viewType === 'detail'"></catalog-detail>
		<aside class="aside aside--right" v-if="favorites && Object.keys(sectionFavorites).length">
			<div class="aside__sticky">
				<div class="favorite-nav">
					<div class="favorite-nav__item" v-for="section in sectionFavorites" v-if="section.name">
						<span class="t">{{ section.name }}</span>

						<span class="n">{{ section.count }}</span>
					</div>
				</div>
			</div>
		</aside>
	</section>
</template>

<script>
    import CatalogSections from '@/components/catalog/CatalogSections.vue'
    import CatalogList from '@/components/catalog/CatalogList.vue'
    import CatalogDetail from '@/components/catalog/CatalogDetail.vue'
    import { mapState } from 'vuex'
    import Tooltip from '@/components/Tooltip.vue';
    import { copyTextToClipboard } from "@/helpers/util/helper";
		import AsideSticky from '@/components/AsideSticky.vue';

    export default {
		props: {
            favorites: {
		        default: function () {
					return false
                }
			}
		},
		name: "Catalog",
		components: {
			CatalogSections,
            CatalogList,
            CatalogDetail,
            Tooltip,
						AsideSticky,
		},
		data: function(){
			return {
				fixed: false,
			}
		},
        created () {
            this.init()
		},
		mounted () {

		},

    computed: {
        totalCoupons () {
            return this.$store.getters['actions/getTotalCoupons'];
        },
        sectionsCountNotEmpty () {
            return this.$store.getters['actions/getSectionsCountNotEmpty'];
        },
        viewType () {
            if(this.$route.params.sid > 0){
			return 'list';
		} else if(this.$route.params.id > 0) {
                return 'detail';
		} else {
                return 'list';
		}
        },
		...mapState({
			currentCity: state => state.cities.current,
	            popups: state => state.popups.items,
	            sort: state => state.actions.sort,
	            sectionFavorites: state => state.actions.favorites.section,
	            sections: state => state.actions.categories
		}),
    },
		methods:{
            openCitySelect() {
                this.$store.dispatch('popups/open', {name: this.popups.citySelect.name});
            },
			init() {
                this.$store.dispatch('actions/initialCatalogState', {route: this.$route});
            },
            copyLink(){
                copyTextToClipboard('https://zemskidki.ru/about/');
                setTimeout(()=>{
                    this.$refs.tooltipCopyLink.tooltipClose();
				},500)
			},
		}
  }
</script>
