                    <a href="javascript:void(0)" @click="activeSection(backSection.id)" class="nav-top__back link-icon">
                        <span>{{ backSection.name }}</span>
                    </a>

                    <a class="ml8 mt-1 link-icon" target="_blank"
                       v-if="user.access_rights && user.access_rights.is_moderator"
                       :href="'/moderator/coupons/id'+action.id+'/#open_catalog_detail'">
                        <span class="i i-pencil2"></span>
                    </a>
                </div>


                <div class="nav-top__r">
                </div>
            </div>


            <div class="main-title">
                <div class="main-title__wrap">
                    <div class="text-size-xxxl text-bold">
                        <template>{{ action.name }}</template>

                        <a class="ml8 mt-1 link-icon" target="_blank"
                           v-if="user.access_rights && user.access_rights.is_moderator"
                           :href="'/moderator/coupons/edit'+action.id+'/'">
                            <span class="i i-pencil2"></span>
                        </a>

                        <a v-else @click="setFavorite()" href="javascript:;" class="ml8 link-icon link-favorite"  :class="{active: action.favorite}">
                            <span class="i i-star"></span>
                        </a>
                    </div>
                </div>
                <div class="main-title__right"></div>
            </div>


            <section class="container">
                <section class="container__left container-scroll">
                            <div>
                        <gallery v-if="action.aboutActionPhoto.length > 0" :items="action.aboutActionPhoto" type="photo-coupons" :firstphoto="action.image_for_viewer" :showFirstPhoto="showFirstPhoto"></gallery>

                                <div class="action-detail__info" v-if="action.aboutActionPromo.length > 0">
                                    <div class="text" v-html="action.aboutActionPromo"></div>
                                </div>



                                <div class="action-detail__info" v-if="action.aboutActionInclude.length > 0">
                                    <div class="title">Скидка распространяется на:</div>

                                    <div class="text" v-html="action.aboutActionInclude"></div>
                                </div>



                                <div class="action-detail__info" v-if="action.aboutActionExclude.length > 0">
                                    <div class="title">Скидка НЕ распространяется на:</div>
                                    <div class="text" v-html="action.aboutActionExclude"></div>
                                </div>



                                <div class="action-detail__info" v-if="action.aboutActionPrivileges.length > 0">
                                    <div class="title">Дополнительные льготы</div>
                                    <div class="text" v-html="action.aboutActionPrivileges"></div>
                                </div>



                                <div class="action-detail__attention" v-if="action.aboutActionWarning.length > 0">
                                    <div class="title">Обратите внимание!</div>


                                    <div class="text" v-html="action.aboutActionWarning"></div>
                                </div>


                                <div class="action-detail__info" v-if="action.aboutActionMore.length > 0">
                                    <div class="title">Дополнительная информация</div>
                                    <div class="text" v-html="action.aboutActionMore"></div>
                                </div>



                                <div>
                                    <div class="action-detail__how">
                                        <div class="title">Как воспользоваться скидкой?</div>

                                        <div class="item">
                                            <ol>
                                                <li><a href="javascript:void(0);" class="link">Оформить купон</a>, указав номер телефона.</li>
                                                <li>В течение дня мы с вами свяжемся, проконсультируем и поможем оформить заказ.</li>
                                            </ol>
                                        </div>
                                        <div class="item time">
                                            <span class="i i-clock2"></span>
                                            <span>Мы обрабатываем заказы с понедельника по пятницу, с 18:00 до 21:00 по Московскому времени.</span>
                                        </div>
                                    </div>
                                </div>
                            </div>







                    <popup :is-set-styled="true" :show="popups.mailZems.show" :name="popups.mailZems.name">
                        <mail-zems :default-text="'Мне предоставили скидку по акции «'+action.name+'» без оформления купона. Дело было так...'" v-if="typeReport === 'notUse'"></mail-zems>
                        <coupon-claim v-else :action="action"></coupon-claim>
                    </popup>




                    <!-- статистика
                    <div class="tab-content">
                        <statistics-detail :action="action"></statistics-detail>
                    </div> -->

