                                  <div v-if="action.banReason && !action.banProvider">
                                      Модератор заблокировал<br>эту акцию.
                                      Причина: {{ action.banReason }}
                                  </div>

                                  <div v-else-if="action.banReason && action.banProvider">
                                      Модератор заблокировал данного<br>
